.main-page {
  &--error {
    margin-top: -88px;
    padding: 51px $p-desktop;
    min-height: 658px;
    color: $color-default-white;

    @include conic-gradient(130.31deg, calc(50% - 465px) 257px);

    @include vp-1199 {
      padding: 51px $p-tablet;
    }

    @include vp-767 {
      padding: 51px $p-mobile;
    }
  }
}

.main-page__title-error {
  margin: 0;
  padding: 90px;
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 72px;
  line-height: normal;
  letter-spacing: 3.6px;
  text-align: center;

  @include vp-767 {
    padding: 65px;
    font-size: 27px;
  }
}
